import React, { useState } from "react";
import "./booknow.css";
import TaskAltIcon from '@mui/icons-material/TaskAlt';
import truck from '../../assets/JOP.gif'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import { Navbar, SendingForm, ReceivingForm, PackageForm } from "../../components";
import { Footer } from "../../containers";
import { Stack ,Stepper, Step, StepLabel, Button, Container } from "@mui/material";
import { Link } from "react-router-dom";

function Booknow() {
  // Define the steps for the stepper
  const steps = ["Sending Form", "Receiving Form", "Package Form"];

  // State to keep track of the current step
  const [activeStep, setActiveStep] = useState(0);

  // Function to handle next step
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  // Function to handle previous step
  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  return (
    <div className="body_app">
      <div className="head_nav">
        <Navbar />
      </div>

      <div className="body-content">
        <Container sx={{ "& .MuiStepLabel-label.MuiStepLabel-alternativeLabel": { color:'#fff', fontSize:'1em' } }}>
          <Stepper activeStep={activeStep} alternativeLabel>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          {activeStep === 0 && <SendingForm />}
          {activeStep === 1 && <ReceivingForm />}
          {activeStep === 2 && <PackageForm />}
        </Container>

        <Container sx={{"& .MuiStack-root": { display:'flex', alignContent:'center', justifyContent:'flex-end', py:'20px', px:'11vw'}}}>
            {activeStep === steps.length ? (
              <div className="finish-form">
                <div className="finish-form-text">
                  <TaskAltIcon style={{ color:'#2CC100', paddingRight:'10px', fontSize:50 }}/>
                  <h2 >Completed</h2>
                </div>
                <img src={truck} alt="Finished Form"></img>
                <Link to="/"><Button onClick={() => setActiveStep(0)} sx={{bgcolor:'#2D3648'}} variant="contained">Go Back</Button></Link>
              </div>
            ) : (
              <Stack direction="row" spacing={2}>
                <Button variant='contained' startIcon={<NavigateBeforeIcon/>} sx={{bgcolor:'#2D3648'}} disabled={activeStep === 0} onClick={handleBack}>Back</Button>
                <Button variant='contained' endIcon={<NavigateNextIcon/>} sx={{bgcolor:'#2D3648'}} onClick={handleNext}>{activeStep === steps.length - 1 ? "Finish" : "Next"}</Button>
              </Stack>
            )}
          </Container>
      </div>

      <Footer />
    </div>
  );
}

export default Booknow;
