import React from "react";
import "./contactus.css";
import { Navbar } from "../../components";
import { Footer } from "../../containers";
import { Divider } from "@mui/material";
import CallIcon from "@mui/icons-material/Call";
import ChatBubbleOutlineIcon from "@mui/icons-material/ChatBubbleOutline";
import MailOutlineIcon from "@mui/icons-material/MailOutline";

const Contactus = () => {
  return (
    <>
      <div className="body_app">
        <div className="head_nav">
          <Navbar />
        </div>

        <div className="contact-info">
          <div className="phone">
            <CallIcon sx={{ fontSize: { xs: 80, sm:100, md:200 } }} />
            <div className="phone-content">
              <h1>Phone Support</h1>
              <h3>+63-910-555-7170</h3>
              <h3>+63-923-555-2995</h3>
              <p>
                "Our dedicated support team is just a phone call away. We
                understand that sometimes you need to talk to a real person, and
                we're here to help. Whether you have questions about our
                services, our friendly and knowledgeable team is ready to assist
                you."
              </p>
            </div>
          </div>

          <Divider sx={{ background: "#1e2636" }} flexItem />

          <div className="message">
            <div className="message-content">
              <h1>Message Support</h1>
              <h3>@turboexpress</h3>
              <p>
                "Can't talk right now? No problem! Our message support option
                allows you to get in touch with us at your convenience. Simply
                type your questions or concerns in the chat box, and one of our
                dedicated support agents will get back to you promptly."
              </p>
            </div>
            <ChatBubbleOutlineIcon sx={{ fontSize: { xs: 80, sm:100, md:200 } }} />
          </div>

          <Divider sx={{ background: "#1e2636" }} flexItem />

          <div className="email">
            <MailOutlineIcon sx={{ fontSize: { xs: 80, sm:100, md:200 } }} />
            <div className="email-content">
              <h1>Mail Support</h1>
              <h3>@customercare</h3>
              <p>
                "If you prefer to reach out through e-mail, we're here to assist
                you. Send us a letter with your questions or concerns, and our
                dedicated support team will respond promptly."
              </p>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    </>
  );
};

export default Contactus;
