import React from 'react';
import './cookiepolicy.css';
import { Navbar } from "../../components";
import { Footer } from "../../containers";

const Cookiepolicy = () => {
  return (
    <>    
          <div className="body_app">
              <div className="head_nav">
                  <Navbar />
              </div>
              <div className='CP_padding'>
                <div className='HeadCookie'>
                  <h1>Cookie Policy</h1>
                  <p>
                    Last Updated: 10/09/23
                  </p>
                </div>
              <div className="CP_Container">
                <div className="CP_content">
                  
                  <p>
                    <span>Welcome to TurboExpress.</span> This Cookie Policy explains how we use cookies
                    and similar technologies when you visit our website. By using our
                    Website, you consent to the use of cookies as described in this
                    policy.
                  </p>
                  <h3>What Are Cookies?</h3>
                    <p>
                      Cookies are small text files that are stored on your device when you
                      visit a website. They help the website recognize your device and
                      remember certain information about your visit.
                    </p>
                  <h3>Types of Cookies We Use</h3>
                  <ul>
                    <li>
                      Essential Cookies: These cookies are necessary for the basic
                      functionality of our Website. They enable you to navigate and use our
                      features.
                    </li>
                    <li>
                      Analytical/Performance Cookies: These cookies allow us to analyze how
                      visitors use our Website and monitor its performance. This helps us
                      improve the user experience.
                    </li>
                    <li>
                      Functionality Cookies: These cookies enable enhanced functionality
                      and personalization. They may remember your preferences or customize
                      the content for you.
                    </li>
                    <li>
                      Third-Party Cookies: We may also use third-party services that may set
                      cookies on your device. These third-party services have their own
                      privacy policies and may use cookies in accordance with their policies.
                    </li>
                  </ul>
                  <h3>How We Use Cookies</h3>
                  <p> We use cookies for the following purposes:</p>
                  <ul>
                    <li>To provide and maintain our Website.</li>
                    <li>To improve and personalize your experience.</li>
                    <li>To understand how our Website is used and make improvements.</li>
                    <li>To show you relevant content and advertisements.</li>
                  </ul>
                  <h3>How to Manage Cookies</h3>
                    <p>
                      You can control and manage cookies in your browser settings. Please note
                      that disabling certain cookies may affect the functionality of our
                      Website.
                    </p>
                  <h3>Changes to This Policy</h3>
                    <p>
                      We may update this Cookie Policy from time to time to reflect changes in
                      our practices or for other operational, legal, or regulatory reasons.
                      We will post the updated policy on our Website with a new "Last Updated"
                      date.
                    </p>
                </div>

              </div>  
            </div>
              <Footer />
          </div>
    </>
  );
};

export default Cookiepolicy;
