import React from "react";
import "./service.css";
import Package from "../../assets/turbo-package.png";
import PackageGrowth from "../../assets/Head2.jpg";
import { Navbar, Booknowbtn, CarouselServices } from "../../components";
import { Footer } from "../../containers";
import { Link } from "react-router-dom";
import { Divider } from "@mui/material";
import LocalShippingIcon from "@mui/icons-material/LocalShipping";
import DirectionsBoatIcon from "@mui/icons-material/DirectionsBoat";
import FlightTakeoffIcon from "@mui/icons-material/FlightTakeoff";
import Groups2Icon from "@mui/icons-material/Groups2";

const ServicePage = () => {
  return (
    <>
      <div className="body_app">
        <div className="head_nav">
          <Navbar />
        </div>

        <div className="services-body">
          <div className="services-main">
            <h1>Fast and Reliable Shipping Courier in the Philippines</h1>
            <p>
              Put your trust in the premier courier service in the Philippines -
              TurboExpress. Send and receive packages anytime, anywhere with our
              secure and dependable courier pickup services.
            </p>
            <div className="booknow-btn">
              <Link to="/booknow">
                <Booknowbtn />
              </Link>
            </div>
          </div>

          <Divider sx={{ background: "#1e2636" }} flexItem />

          <div className="service-title1">
            <div className="service-title-content1">
              <h2>Your Premier Courier Service</h2>
              <p>
                TurboExpress takes immense pride in ensuring your goods reach
                their destination promptly and reliably within the Philippines.
                We are dedicated to guaranteeing the secure and safe delivery of
                your parcels, documents, and goods. Wherever you may be across
                the globe, rest assured that our shipping services are designed
                to meet the needs of both you and your cherished ones.
              </p>
            </div>
            <img src={Package} alt="Package Pic"></img>
          </div>

          <div className="service-title2">
            <img src={PackageGrowth} alt="Package Pic 2"></img>
            <div className="service-title-content2">
              <h2>Fueling Business Growth and Success</h2>
              <p>
                TurboExpress has been instrumental in revolutionizing
                businesses, enabling them to extend their reach, bolster their
                profits, and escalate their revenue, all while minimizing costs
                through our competitive courier service rates. Each solution we
                offer is meticulously tailored to cater to the unique
                requirements of every business, ensuring you receive the
                dedicated attention necessary for thriving and achieving
                success.
              </p>
            </div>
          </div>

          <Divider sx={{ background: "#1e2636" }} flexItem />

          <div className="container-service-m">
            <h1>The Philippines’ Leading Courier Services Provider</h1>
            <h4>
              TurboExpress offers a variety of delivery and courier and pick-up
              services in the Philippines
            </h4>

            <div className="card-container-all">
              <div className="card-container-service">
                <LocalShippingIcon sx={{ fontSize: 60 }} />
                <p>
                  TurboExpress fulfills your shipping needs with its land cargo
                  and freight solutions. Whether its documents, packages, or
                  shipments, you can trust your cargo to arrive swiftly and
                  securely.
                </p>
              </div>
              <div className="card-container-service">
                <DirectionsBoatIcon sx={{ fontSize: 60 }} />
                <p>
                  Sea cargo and freight services of TurboExpress guarantee you
                  peace of mind. With our optimal sea routes and accurate
                  schedule, we promise to always deliver your shipments on
                  schedule.
                </p>
              </div>
              <div className="card-container-service">
                <FlightTakeoffIcon sx={{ fontSize: 60 }} />
                <p>
                  TurboExpress air freight and cargo solutions is the fastest
                  way to send documents, parcels, and shipments with shipment
                  time taking as fast as 1 day.
                </p>
              </div>
            </div>
          </div>

          <Divider sx={{ background: "#1e2636" }} flexItem />

          <div className="testimonials-con">
            <h1>Testimonials</h1>
            <Groups2Icon sx={{ fontSize: { sm: 200, xs: 100 } }} />
          </div>
          <CarouselServices />
        </div>

        <Footer />
      </div>
    </>
  );
};

export default ServicePage;
