import React from "react";
import "./privacypolicy.css";
import { Navbar } from "../../components";
import { Footer } from "../../containers";

const Privacypolicy = () => {
  return (
    <>
      <div className="body_app">
        <div className="head_nav">
          <Navbar />
        </div>

        <div className="PP_padding">
          <div className="HeadPrivacy">
            <h1>Privacy Policy</h1>
            <p>Last Updated: 10/09/23</p>
          </div>
          <div className="PP_Container">
            <div className="PP_content">
              <br></br>
              <p>
                <span>Welcome to TurboExpress.</span> We are committed to protecting your
                privacy. This Privacy Policy outlines how we collect, use,
                disclose, and safeguard your personal information when you use
                our services and visit our website.
              </p>
              <h3>Information We Collect</h3>
              <ul>
                <li>
                  Personal Information: We may collect personal information,
                  including but not limited to, your name, contact information,
                  delivery addresses, and payment details, when you use our
                  services or interact with our website.
                </li>
                <li>
                  Usage Data: We may automatically collect information about how
                  you use our services, including your IP address, device type,
                  browser type, and operating system.
                </li>
              </ul>
              <h3>How We Use Your Information</h3>
              <p>
                We may use the information we collect for purposes including:
              </p>
              <ul>
                <li>Providing and improving our services</li>
                <li>Communicating with you</li>
                <li>Processing payments</li>
                <li>Personalizing your experience</li>
                <li>Complying with legal obligations</li>
              </ul>
              <h3>Sharing of Your Information</h3>
              <p>We may share your information with:</p>
              <ul>
                <li>
                  Third-party service providers who assist us in providing our
                  services.
                </li>
                <li>
                  Affiliates and business partners for operational purposes.
                </li>
                <li>
                  In response to legal requests or as required by applicable
                  law.
                </li>
              </ul>
              <h3>Security</h3>
              <p>
                We implement security measures to protect your information, but
                no method of transmission over the internet or electronic
                storage is completely secure. We cannot guarantee absolute
                security.
              </p>
              <h3>Changes to this Privacy Policy</h3>
              <p>
                We may update this Privacy Policy from time to time. The revised
                policy will be effective immediately upon posting on our
                Website.
              </p>
              <br></br>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    </>
  );
};

export default Privacypolicy;
