import React from "react";
import "./faqs.css";

const Faqs = () => {
  return (
    <div className="faq-container">
      <h1>FAQ'S</h1>
      <div className="faq-card-container">
        <div className="faq-card">
          <h3>Can you provide insurance for my shipment?</h3>
          <p>
            Yes, we offer insurance options to provide an extra layer of
            protection for your goods during transit. Our team can assist you in
            selecting the appropriate coverage for your specific shipment.
          </p>
        </div>
        <div className="faq-card">
          <h3>Can I get real-time updates on the location of my shipment?</h3>
          <p>
            Yes, our advanced tracking systems provide real-time updates on the
            location and status of your shipment. You can access this
            information through our online portal or by contacting our support
            team.
          </p>
        </div>
        <div className="faq-card">
          <h3>How do you ensure the safety of my goods during transit?</h3>
          <p>
            We prioritize the safety of your goods. Our logistics team employs
            industry-standard packaging, secure storage facilities, and
            experienced handlers to ensure your items are protected throughout
            the transportation process.
          </p>
        </div>
      </div>
    </div>
  );
};

export default Faqs;
