import React from "react";
import "./login.css";
import Navbar from "../navbar/Navbar";
import { Footer } from "../../containers";
import { Box, Button, Checkbox, FormControlLabel, TextField } from "@mui/material";
import { Link } from "react-router-dom";

const Login = () => {
  return (
    <>
      <div className="body_app">
        <div className="head_nav">
          <Navbar />
        </div>

        <div className="login-container">
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: "15px",
              py: "2em",
              px: "2em",
              bgcolor: "#fff",
              boxShadow: "0px 0px 45px 1px rgba(149,179,168,1)",
              flexDirection: "column",
              "& .MuiTextField-root": { m: 1 },
            }}
          >
            <h2>Login</h2>

            <TextField
              required
              id="email"
              label="Email"
              variant="outlined"
              sx={{ width: { xs: "100%", sm: "30vw" } }}
            />
            <TextField
              required
              id="password"
              type="password"
              label="Password"
              variant="outlined"
              sx={{ width: { xs: "100%", sm: "30vw" } }}
            />

            <div className="group-feature">
                <FormControlLabel
                control={<Checkbox />}
                label="Remember Me"
                />
                <Button href="">Forgot Password?</Button>
            </div>

            <Link to="/" style={{ textDecoration: 'none', width:'100%'}}><Button variant="contained" sx={{ bgcolor:'#2D3648', width: '100%' }} >Login</Button></Link>
          </Box>
        </div>

        <Footer />
      </div>
    </>
  );
};

export default Login;
