import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";
import "swiper/css";
import 'swiper/css/pagination';
import "swiper/css/navigation";
import "./carousel.css";

const CarouselServices = () => {
  return (
    <Swiper
        slidesPerView={1}
        centeredSlides={true}
        spaceBetween={70}
        loop={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Pagination, Navigation]}
        className="swiper"
      >
      <SwiperSlide>
        <p>
          "Mahalaga sa amin ang bawat oras at presyo ng pagpapadala. Kaya naman,
          TurboExpress ang aming unang pinipili. Mas pinagaan nito ang aming
          operasyon at mas pinaigting ang aming kita. Salamat, TurboExpress, sa
          inyong matibay at maaasahang serbisyo!"
        </p>
        <h4>-Steve Garciano</h4>
      </SwiperSlide>
      <SwiperSlide>
        <p>
          "Hindi ko maipagkakaila ang kahusayan ng TurboExpress. Sila ay
          nagbibigay ng tapat at mabilis na paghahatid ng aking mga produkto sa
          aking mga kliyente. Ang kanilang mga kawani ay palaging handa na
          tumugon sa aking mga katanungan at nagsisikap na maibigay ang
          pinakamahusay na serbisyo. Hindi ko na kailangang mag-alala sa kalidad
          ng pagpapadala dahil alam kong ligtas at maasahan ang aking mga
          kargamento sa kanila."
        </p>
        <h4>-Raf Saludo</h4>
      </SwiperSlide>
      <SwiperSlide>
        <p>
          "Nais kong ibahagi ang aking napakagandang karanasan sa paggamit ng
          serbisyo ng TurboExpress. Sa kanilang mga kamay, tiyak na nasa
          mabuting kalagayan ang aking mga padala. Napakahusay ng kanilang
          sistema at napakabilis ng kanilang pagproseso. Hindi mo mararamdaman
          ang kaba sa paghihintay dahil alam mong may mga propesyonal na
          nag-aalaga ng iyong kargamento."
        </p>
        <h4>-Johnneri Garcia</h4>
      </SwiperSlide>
    </Swiper>
  );
};

export default CarouselServices;
