import React from "react";
import "./signup.css";
import Navbar from "../navbar/Navbar";
import Footer from "../../containers/footer/Footer";
import { Box, Button, Stack, TextField } from "@mui/material";
import { Link } from "react-router-dom";

const Signup = () => {
  return (
    <>
      <div className="body_app">
        <div className="head_nav">
          <Navbar />
        </div>

        <div className="sign-up-container">
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: "15px",
              py: "2em",
              px: "2em",
              bgcolor: "#fff",
              boxShadow: "0px 0px 45px 1px rgba(149,179,168,1)",
              flexDirection: "column",
              "& .MuiTextField-root": { m: 1, width: { xs: "100%", sm: "30vw" } },
            }}
          >
            <h2>Sign Up</h2>

            <TextField
              required
              id="first-name"
              label="First Name"
              variant="outlined"
            />
            <TextField
              required
              id="last-name"
              label="Last Name"
              variant="outlined"
            />
            <TextField
              required
              id="mobile-number"
              label="Mobile Number"
              variant="outlined"
              inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
            />
            <TextField
              required
              id="email"
              label="Email"
              type="email"
              variant="outlined"
            />

            <TextField
              required
              id="password"
              label="Password"
              type="password"
              variant="outlined"
            />
            <TextField
              required
              id="password"
              label="Confirm Password"
              type="password"
              variant="outlined"
            />

            <div className="sign_up-btn">
              <Stack
                direction={{ xs: "column", sm: "row" }}
                spacing={{ xs: 2, sm: 4, md: 6, lg: 30 }}
                sx={{ display: "flex", alignItems: "center" }}
              >
                <Link to="/login">
                  <Button
                    variant="outlined"
                    sx={{
                      fontSize: "0.8em",
                      borderColor: "#2D3648",
                      color: "#2D3648",
                    }}
                  >
                    Have an account? Sign in
                  </Button>
                </Link>
                <Link to="/">
                  <Button variant="contained" sx={{ bgcolor: "#2D3648" }}>
                    Register
                  </Button>
                </Link>
              </Stack>
            </div>
          </Box>
        </div>

        <Footer />
      </div>
    </>
  );
};

export default Signup;
