import React from 'react';
import ReactDOM from 'react-dom';
import { createBrowserRouter, RouterProvider } from 'react-router-dom';
import App from './App';
import './index.css'
import { Booknow, ServicePage, Contactus, Privacypolicy, Termscondition, Cookiepolicy, AccountPage } from './pages';
import { Signup, Login } from './components';

const router = createBrowserRouter([
    {
      path: "/",
      element: <App />,
    },
    {
      path: "booknow",
      element: <Booknow />,
    },
    {
      path: "signup",
      element: <Signup />,
    },
    {
      path: "login",
      element: <Login />,
    },
    {
      path: "services",
      element: <ServicePage />,
    },
    {
      path: "contact",
      element: <Contactus />,
    },
    {
      path: "privacypolicy",
      element: <Privacypolicy />,
    },
    {
      path: "termscondition",
      element: <Termscondition />,
    },
    {
      path: "cookie",
      element: <Cookiepolicy />
    },
    {
      path: "account",
      element: <AccountPage />,
    },
  ]);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <RouterProvider router={router} />
);