import React, { useState } from "react";
import "./account.css";
import { Navbar } from "../../components";
import { Footer, Faqs } from "../../containers";
import { Button, Stack, TextField, FormControl, Avatar } from "@mui/material";

const AccountPage = () => {
  const [showEditProfile, setShowEditProfile] = useState(true);
  const [showTrackPackage, setShowTrackPackage] = useState(false);
  const [showParcels, setShowParcels] = useState(false);

  const handleEditProfileClick = () => {
    setShowEditProfile(true);
    setShowTrackPackage(false);
    setShowParcels(false);
  };

  const handleParcelsClick = () => {
    setShowEditProfile(false);
    setShowTrackPackage(false);
    setShowParcels(true);
  };

  const handleTrackPackageClick = () => {
    setShowTrackPackage(true);
    setShowEditProfile(false);
    setShowParcels(false);
  };

  return (
    <div className="body_app">
      <div className="head_nav">
        <Navbar />
      </div>

      <div className="account-page-content">
        <div className="account-item-content">
          <div className="side-btn">
            <Stack direction="row" spacing={2}>
              <Button
                variant="contained"
                sx={{ bgcolor: "#2D3648", fontSize:'0.7em' }}
                onClick={handleParcelsClick}
              >
                Parcels
              </Button>
              <Button
                variant="contained"
                sx={{ bgcolor: "#2D3648", fontSize:'0.7em' }}
                onClick={handleTrackPackageClick}
              >
                Track Package
              </Button>
              <Button
                variant="contained"
                sx={{ bgcolor: "#2D3648", fontSize:'0.7em' }}
                onClick={handleEditProfileClick}
              >
                Edit Profile
              </Button>
            </Stack>
          </div>

          <div className="account-form">
            {showEditProfile && <EditProfile />}
            {showTrackPackage && <TrackPackage />}
            {showParcels && <Parcels />}
          </div>
        </div>

        <Faqs />
      </div>

      <Footer />
    </div>
  );
};

function EditProfile() {
  return (
    <>
      <div className="edit-profile-form">
        <FormControl fullWidth>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <div style={{ display: "flex", flexDirection: "row" }}>
              <TextField
                label="First Name"
                variant="outlined"
                style={{ flex: 1, margin: "1em" }}
              />
              <TextField
                label="Last Name"
                variant="outlined"
                style={{ flex: 1, margin: "1em" }}
              />
            </div>
            <TextField
              type="email"
              label="Email"
              variant="outlined"
              style={{ flex: 1, margin: "1em" }}
            />
            <TextField
              label="Address"
              variant="outlined"
              style={{ flex: 1, margin: "1em" }}
            />
            <div style={{ display: "flex", flexDirection: "row" }}>
              <TextField
                label="Mobile Number"
                variant="outlined"
                style={{ flex: 1, margin: "1em" }}
              />
              <TextField
                label="Province"
                variant="outlined"
                style={{ flex: 1, margin: "1em" }}
              />
            </div>
            <div style={{ display: "flex", flexDirection: "row" }}>
              <TextField
                label="City"
                variant="outlined"
                style={{ flex: 1, margin: "1em" }}
              />
              <TextField
                label="Barangay"
                variant="outlined"
                style={{ flex: 1, margin: "1em" }}
              />
            </div>
            <TextField
              type="password"
              label="Password"
              variant="outlined"
              style={{ flex: 1, margin: "1em" }}
            />
            <TextField
              type="password"
              label="Confirm Password"
              variant="outlined"
              style={{ flex: 1, margin: "1em" }}
            />
          </div>
        </FormControl>

        <div className="avatar-form">
          <Avatar sx={{ width: 150, height: 150, fontSize: "3em", m: 2 }}>
            H
          </Avatar>
        </div>
      </div>
      <div className="save-btn">
        <Stack direction="row" spacing={2}>
          <Button variant="contained" sx={{ bgcolor: "#74747B" }}>
            Cancel
          </Button>
          <Button variant="contained" sx={{ bgcolor: "#475F8D" }}>
            Save
          </Button>
        </Stack>
      </div>
    </>
  );
}

function TrackPackage() {
  return (
    <>
      <div className="tracking-container">
          <FormControl fullWidth>
            <TextField
              type="search"
              label="Enter Tracking Number"
              variant="filled"
              style={{ flex:1 }}
            />
          </FormControl>
        <div className="tracking-form">
          <h1>Package Tracking Information Here</h1>
        </div>
      </div>
    </>
  );
}

function Parcels() {
  return (
    <>
      <div className="tracking-container">
        <div className="search-form">
          <TextField type="search" label="Search Parcel" variant="filled" />
        </div>
        <div className="tracking-form">
          <h1>Parcel Table Here</h1>
        </div>
        <div className="next-btn">
          <Stack direction="row" spacing={2}>
            <Button variant="contained" sx={{ bgcolor: "#74747B" }}>
              Previous
            </Button>
            <Button variant="contained" sx={{ bgcolor: "#475F8D" }}>
              Next
            </Button>
          </Stack>
        </div>
      </div>
    </>
  );
}

export default AccountPage;
