import React, { useState } from 'react'
import CloseIcon from '@mui/icons-material/Close';
import MenuIcon from '@mui/icons-material/Menu';
import './navbar.css'
import logo from '../../assets/TurboExp-Logo.png'
import AvatarMenu from './AvatarMenu'
import { Link } from 'react-router-dom';

const MenuLinks = () => (
  <>
    <p><Link to="/booknow">Book Now</Link></p>
    <p><Link to="/contact">Contact Us</Link></p>
    <p><Link to="/services">Services</Link></p>
  </>
)

const Navbar = () => {
  const [toggleMenu, setToggleMenu] = useState(false);

  return (
    <>
    <div className='turbo_navbar'>
        <div className='turbo_navbar-links'>
            <div className='turbo_navbar-links_logo'>
                <Link to="/"><img src={logo} alt="Turbo Express" /></Link>
            </div>

            <div className='turbo_navbar-links_container'>
              <MenuLinks />
              <AvatarMenu />
            </div>

            <div className='turbo_ham-menu'>
              { toggleMenu
                ? <CloseIcon sx={{ color: '#A0ABC0' }} fontSize='large' onClick={() => setToggleMenu(false)} />
                : <MenuIcon sx={{ color: '#A0ABC0' }} fontSize='large' onClick={() => setToggleMenu(true)} />
              }
              { toggleMenu && (
                <div className='turbo_ham-menu_container scale-up'>
                  <div className='turbo_ham-menu_container2'>
                    <AvatarMenu />
                  </div>

                  <div className='turbo_ham-menu_container-links'>
                    <MenuLinks />
                  </div>
                </div>
              )}
            </div>
        </div>
    </div>
    </>
  )
}

export default Navbar