import React, { useState } from "react";
import "./receivingform.css";
import {
  Container,
  FormLabel,
  TextField,
  Box,
  FormGroup,
  InputLabel,
  Select,
  MenuItem,
  FormControl,
  Grid,
} from "@mui/material";
import HelpOutlineIcon from "@mui/icons-material/HelpOutline";

const packageTypes = [
  { value: "small", label: "Small Sachet", price: "\u20B1 40-59" },
  { value: "medium", label: "Medium Sachet", price: "\u20B1 60-79" },
  { value: "large", label: "Large Sachet", price: "\u20B1 80-99" },
  { value: "small-box", label: "Small Box", price: "\u20B1 110-139" },
  { value: "medium-box", label: "Medium Box", price: "\u20B1 140-169" },
  { value: "large-box", label: "Large Box", price: "\u20B1 170-199" },
];

const itemTypes = [
  { value: "fragile", label: "Fragile" },
  { value: "rigid", label: "Rigid" },
];

const ReceivingForm = () => {
  const [packageType, setPackageType] = useState("");
  const [itemType, setItemType] = useState("");

  const handlePackageTypeChange = (event) => {
    setPackageType(event.target.value);
  };

  const handleItemTypeChange = (event) => {
    setItemType(event.target.value);
  };

  return (
    <>
      <Container
        className="method-container-r"
        maxWidth="lg"
        sx={{ padding: "0", pt: "50px" }}
      >
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            flexWrap: "wrap",
            bgcolor: "#D2D2D2",
            padding: "5%",
            borderRadius: "10px",
          }}
        >
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <Box>
                <FormLabel id="package-details">Package Details</FormLabel>
                <FormGroup
                  aria-labelledby="package-details"
                  name="form-textfield"
                >
                  <TextField multiline label="Package Description" variant="outlined" style={{ marginTop:'15px' }}/>

                  <div
                    style={{
                      display: "flex",
                      flexDirection: "row",
                      marginTop: "15px",
                    }}
                  >
                    <FormControl style={{ flex: 1, marginRight: "15px" }}>
                      <InputLabel htmlFor="package-type">
                        Package Type
                      </InputLabel>
                      <Select
                        label="Package Type"
                        value={packageType}
                        onChange={handlePackageTypeChange}
                        inputProps={{
                          name: "package-type",
                          id: "package-type",
                        }}
                      >
                        {packageTypes.map((item) => (
                          <MenuItem key={item.value} value={item.value}>
                            {item.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>

                    <FormControl style={{ flex: 1 }}>
                      <InputLabel htmlFor="item-type">Item Type</InputLabel>
                      <Select
                        label="Item Type"
                        value={itemType}
                        onChange={handleItemTypeChange}
                        inputProps={{
                          name: "item-type",
                          id: "item-type",
                        }}
                      >
                        {itemTypes.map((item) => (
                          <MenuItem key={item.value} value={item.value}>
                            {item.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                  </div>
                </FormGroup>
                <Box
                  sx={{
                    display: "flex",
                    my: {sm:"5em", xs:'1em', },
                    bgcolor: "#717D96",
                    p: "10px",
                    borderRadius: "5px",
                    fontSize: "1em",
                  }}
                >
                  <HelpOutlineIcon sx={{ mr: "5px" }} />
                  <p>
                    Book before 11PM today to have your parcels picked up the
                    next business day.
                  </p>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Box
                sx={{
                  bgcolor: "#F0F0F0",
                  padding: "5%",
                  borderRadius: "10px",
                  textAlign: "center",
                }}
              >
                <h2>Price Listing</h2>
                <ul
                  style={{ listStyle: "none", textAlign: "left",  }}
                >
                  {packageTypes.map((item) => (
                    <li key={item.value}>
                      <span>{item.label}</span>
                      <span style={{ float: 'right' }}>{item.price}</span>
                    </li>
                  ))}
                </ul>
                <Box
                  sx={{
                    display: "flex",
                    my: "25px",
                    bgcolor: "#717D96",
                    p: "10px",
                    borderRadius: "5px",
                    fontSize: "1em",
                  }}
                >
                  <HelpOutlineIcon sx={{ mr: "5px" }} />
                  <p style={{ textAlign: "left" }}>
                    Price List are Applicable Nationwide
                  </p>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Container>
    </>
  );
};

export default ReceivingForm;
