import React, { useState } from "react";
import "./receivingform.css";
import {
  Container,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormLabel,
  FormControl,
  TextField,
  Box,
} from "@mui/material";
import HelpOutlineIcon from "@mui/icons-material/HelpOutline";

const ReceivingForm = () => {
  const [selectedOption, setSelectedOption] = useState("deliver");

  const handleOptionChange = (event) => {
    setSelectedOption(event.target.value);
  };

  return (
    <>
      <Container
        className="method-container-r"
        maxWidth="md"
        sx={{ padding: "0", pt: "50px" }}
      >
        <Box sx={{ bgcolor: "#D2D2D2", padding: "5%", borderRadius: "10px" }}>
          <FormControl>
            <FormLabel id="sending-radio-btn" sx={{ color: "#0072AA" }}>
              Sending Method
            </FormLabel>
            <RadioGroup
              row
              aria-labelledby="sending-radio-btn"
              name="radio-btn-group"
              value={selectedOption}
              onChange={handleOptionChange}
              sx={{ mb: "40px" }}
            >
              <FormControlLabel
                value="deliver"
                control={<Radio />}
                label="Deliver"
              />
              <FormControlLabel
                value="drop-off"
                control={<Radio />}
                label="Drop-Off"
              />
            </RadioGroup>
          </FormControl>
          {selectedOption === "deliver" && <DeliverForm />}
          {selectedOption === "drop-off" && <DropoffForm />}

          <Box
            sx={{
              display: "flex",
              my: "25px",
              bgcolor: "#717D96",
              p: "10px",
              borderRadius: "5px",
              fontSize: "1em",
            }}
          >
            <HelpOutlineIcon sx={{ mr: "5px" }} />
            <p>
              Book before 11PM today to have your parcels pickup the next
              business day.
            </p>
          </Box>
        </Box>
      </Container>
    </>
  );
};

function DeliverForm() {
  return (
    <>
      <Container
        id="deliver-form"
        maxWidth="md"
        sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}
      >
        <FormControl fullWidth>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              "& .MuiTextField-root": { m: 1 },
            }}
          >
            <FormLabel id="receiver-details">Receiver Details</FormLabel>
            <TextField
              required
              id="first-name"
              label="First Name"
              variant="outlined"
            />
            <TextField
              required
              id="last-name"
              label="Last Name"
              variant="outlined"
            />
            <TextField
              required
              id="mobile-number"
              label="Mobile Number"
              variant="outlined"
              inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
            />
            <TextField
              required
              id="email"
              label="Email"
              type="email"
              variant="outlined"
            />

            <FormLabel id="sender-address" sx={{ mt: "20px" }}>
              Delivery Address
            </FormLabel>
            <TextField
              id="etc"
              label="Floor, Unit, Building, etc."
              variant="outlined"
            />
            <TextField required id="street" label="Street" variant="outlined" />

            <TextField
              required
              id="province"
              label="Province"
              variant="outlined"
            />
            <TextField required id="city" label="City" variant="outlined" />
            <TextField required id="brgy" label="Barangay" variant="outlined" />
            <TextField
              required
              id="zipcode"
              label="Zipcode"
              variant="outlined"
              inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
            />
          </Box>
        </FormControl>
      </Container>
    </>
  );
}

function DropoffForm() {
  return (
    <>
      <Container
        id="dropoff-form"
        maxWidth="md"
        sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}
      >
        <FormControl fullWidth>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              "& .MuiTextField-root": { m: 1 },
            }}
          >
            <FormLabel id="receiver-details">Receiver Details</FormLabel>
            <TextField
              required
              id="first-name"
              label="First Name"
              variant="outlined"
            />
            <TextField
              required
              id="last-name"
              label="Last Name"
              variant="outlined"
            />
            <TextField
              required
              id="mobile-number"
              label="Mobile Number"
              variant="outlined"
              inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
            />
            <TextField
              required
              id="email"
              label="Email"
              type="email"
              variant="outlined"
            />

            <FormLabel id="sender-address" sx={{ mt: "20px" }}>
              Delivery Address
            </FormLabel>
            <TextField
              id="etc"
              label="Floor, Unit, Building, etc."
              variant="outlined"
            />
            <TextField required id="street" label="Street" variant="outlined" />

            <TextField
              required
              id="province"
              label="Province"
              variant="outlined"
            />
            <TextField required id="city" label="City" variant="outlined" />
            <TextField required id="brgy" label="Barangay" variant="outlined" />
            <TextField
              required
              id="zipcode"
              label="Zipcode"
              variant="outlined"
              inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
            />
          </Box>
        </FormControl>
      </Container>
    </>
  );
}

export default ReceivingForm;
